:root {
  --speed: 300ms;
  --time-function: ease-in-out;
  --vh: 1vh;
}

/*=======================START-MEDIA=====================*/
/*=======================END-MEDIA=====================*/
/* media breakpoint mixin */
.video {
  max-width: 230px;
}

@media only screen and (max-width: 540px) {
  .video {
    max-width: 125px;
  }
}

.video__large {
  max-width: 389px;
}

@media only screen and (max-width: 1200px) {
  .video__large {
    max-width: 329px;
  }
}

@media only screen and (max-width: 540px) {
  .video__large {
    max-width: 228px;
  }
}

.video__large .video__preview {
  height: 259px;
}

@media only screen and (max-width: 1200px) {
  .video__large .video__preview {
    height: 219px;
  }
}

@media only screen and (max-width: 540px) {
  .video__large .video__preview {
    height: 151px;
  }
}

.video__preview {
  position: relative;
  cursor: pointer;
  height: 171px;
  transition-timing-function: ease-in-out;
  transition-duration: 300ms;
  transition-property: all;
}

@media only screen and (max-width: 540px) {
  .video__preview {
    height: 102px;
  }
}

.video__preview:before {
  content: '';
  position: absolute;
  z-index: 1;
  background: rgba(0, 0, 0, 0.7);
  width: 100%;
  height: 100%;
  transition-property: all;
  transition-duration: 300ms;
  transition-timing-function: ease-in-out;
}

.video__preview .icon {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: #EF7F1A;
  z-index: 2;
  transition-property: all;
  transition-duration: 300ms;
  transition-timing-function: ease-in-out;
}

@media (any-hover: hover) {
  .video__preview:hover:before {
    opacity: 0.3;
  }
  .video__preview:hover .icon {
    filter: drop-shadow(0px 0px 20px black);
  }
}

.video__preview img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
}

.video__sub-title {
  margin-top: 14px;
  font-size: 14px;
  line-height: 1.14;
  padding-left: 11px;
  text-transform: uppercase;
}

.our-work {
  padding-top: 0px;
  overflow: hidden;
}

.our-work__page {
  padding-bottom: 157px;
  overflow: visible;
}

@media only screen and (max-width: 1200px) {
  .our-work__page {
    padding-bottom: 117px;
  }
}

@media only screen and (max-width: 540px) {
  .our-work__page {
    padding-bottom: 51px;
  }
}

.our-work__page-heading {
  margin-bottom: 26px;
}

.our-work__top {
  margin: -10px;
}

@media only screen and (max-width: 540px) {
  .our-work__top {
    margin: -5px;
  }
}

.our-work__item {
  margin: 10px;
  line-height: 0;
}

.our-work__item img {
  object-fit: cover;
  object-position: center;
}

@media only screen and (max-width: 540px) {
  .our-work__item {
    margin: 5px;
  }
  .our-work__item img {
    height: 102px;
  }
}

@media only screen and (max-width: 540px) {
  .our-work__item .video__preview .icon {
    width: 90px;
    height: 90px;
  }
}

.our-work__disclaimer {
  text-transform: uppercase;
  margin-top: 10px;
  margin-bottom: 50px;
}

@media only screen and (max-width: 1200px) {
  .our-work__disclaimer {
    margin-bottom: 32px;
  }
}

.our-work__top-disclaimer {
  text-transform: uppercase;
  margin-bottom: 31px;
}

@supports (column-gap: 20px) {
  .our-work__bottom {
    column-gap: 20px;
  }
}

@supports not (column-gap: 20px) {
  .our-work__bottom {
    margin-right: calc( 0px - (20px / 2));
    margin-left: calc( 0px - (20px / 2));
  }
  .our-work__bottom > * {
    margin-right: calc(20px / 2);
    margin-left: calc(20px / 2);
  }
}

.our-work__bottom.card {
  padding: 25px;
  margin-top: 58px;
}

@media only screen and (max-width: 1200px) {
  .our-work__bottom.card {
    padding-top: 43px;
    padding-bottom: 43px;
  }
}

@media only screen and (max-width: 540px) {
  .our-work__bottom.card {
    padding: 0px;
    background: rgba(255, 255, 255, 0);
  }
}

.our-work__left {
  max-width: 907px;
}

.our-work__left a {
  color: #FF0000;
  position: relative;
  transition-timing-function: ease-in-out;
  transition-duration: 300ms;
  transition-property: all;
}

.our-work__left a:after {
  content: '';
  position: absolute;
  transform: scaleX(0);
  width: 100%;
  height: 2px;
  background: currentColor;
  transition: inherit;
  transform-origin: left;
  position: absolute;
  bottom: 2px;
  left: 0px;
}

@media (any-hover: hover) {
  .our-work__left a:hover:after {
    transform: scaleX(1);
  }
}

.our-work__left a:focus:after {
  transform: scaleX(1);
}

.our-work__left .icon {
  display: inline;
}

.our-work__left .button {
  flex-shrink: 0;
}

.our-work__right {
  display: flex;
  align-items: center;
  flex-shrink: 0;
}

@media only screen and (max-width: 1200px) {
  .our-work__right {
    margin-top: 10px;
    justify-content: flex-end;
  }
}

.our-work__info {
  text-align: right;
  text-transform: uppercase;
  max-width: 224px;
  margin-right: 24px;
}

@media only screen and (max-width: 540px) {
  .our-work__info {
    display: none;
  }
}

.our-work__icon {
  margin-right: 34px;
}

.our-work__info {
  font-weight: 700;
}

.our-work .slick-track {
  line-height: 0;
}

.our-work__page-row {
  display: grid;
  grid-template-columns: repeat(7, 1fr);
}

@media only screen and (max-width: 1640px) {
  .our-work__page-row {
    grid-template-columns: repeat(6, 1fr);
  }
}

@media only screen and (max-width: 1200px) {
  .our-work__page-row {
    grid-template-columns: repeat(5, 1fr);
  }
}

@media only screen and (max-width: 1000px) {
  .our-work__page-row {
    grid-template-columns: repeat(4, 1fr);
  }
}

@media only screen and (max-width: 540px) {
  .our-work__page-row {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media only screen and (max-width: 540px) {
  .our-work__page-row {
    margin-bottom: 15px;
  }
}

.our-work__page-row .our-work__item {
  max-width: 100%;
}

.our-work__page-row .our-work__item img {
  width: 100%;
}

@media only screen and (max-width: 1200px) and (min-width: 1001px) {
  .our-work__page-row .our-work__item img,
  .our-work__page-row .our-work__item .video__preview {
    height: 157px;
  }
}

@media only screen and (max-width: 1000px) and (min-width: 541px) {
  .our-work__page-row .our-work__item img,
  .our-work__page-row .our-work__item .video__preview {
    height: 127px;
  }
}

.our-work__button {
  margin-top: 33px;
  margin-left: 50%;
  transform: translateX(-50%);
}

.content-page {
  padding-top: 54px;
}

@media only screen and (max-width: 1200px) {
  .content-page {
    padding-top: 50px;
  }
}

@media only screen and (max-width: 540px) {
  .content-page {
    padding-top: 15px;
  }
}

.content-page__title {
  max-width: 380px;
  text-wrap: normal;
}

@media only screen and (max-width: 1200px) {
  .content-page__title {
    max-width: 280px;
  }
}

@media only screen and (max-width: 540px) {
  .content-page__title {
    max-width: 180px;
  }
}

.content-page__title:not(:last-child) {
  margin-bottom: 14px;
}

.content-page__text {
  max-width: 651px;
}
